:root {
  --animation-duration: 250ms;
  --animation-timing-function: cubic-bezier(.4, 0, .2, 1);
  --main-black: #081617;
  --main-blue: #8787d4;
  --text-main: #ca9b4a;
  --text-logo: #ca9b4a;
  --bgc-main: #080f2b;
  --bgc-second: #23301d;
  --bgc-third: rgba(116, 132, 108, .3);
  --bgc-fourth: rgba(2, 48, 45, .5);
  --accent-color: #ebebeb;
  --accent-color-transparent: #c8d6c2;
  --scroll-track-shadow: rgba(0, 0, 0, .3);
  --scroll-thumb-shadow: rgba(0, 0, 0, .5);
}

.color-theme-ligth {
  --text-main: #000;
  --bgc-main: #f0e0e0;
  --accent-color: #271957;
  --main-blue: #6666c0;
}

*, :after, :before {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, pre, samp {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

[type="button"], [type="reset"], [type="submit"], button {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
}

a {
  outline: none;
  text-decoration: none;
}

ul {
  padding: 0;
  list-style-type: none;
}

input {
  outline: none;
  padding: 0;
}

button {
  cursor: pointer;
  border: 0;
  margin: 0;
  padding: 0;
}

