body {
  letter-spacing: .03px;
  background-color: var(--bgc-third);
  font-family: Roboto, sans-serif;
  font-size: 14px;
  line-height: 1.17;
}

html, body {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 440px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 700px;
    padding-left: 34px;
    padding-right: 34px;
  }
}

@media screen and (min-width: 1140px) {
  .container {
    width: 1110px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

.footer-fixed-wrapper {
  height: calc(100% + 1px);
  flex-direction: column;
  padding-top: 60px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer-fixed-wrapper {
    padding-top: 80px;
  }
}

.main-content {
  flex-grow: 1;
}

.logo {
  max-width: 200px;
  letter-spacing: .05em;
  align-items: center;
  padding-top: 7px;
  padding-bottom: 7px;
  font-family: Roboto Slab, sans-serif;
  font-size: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .logo {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.logo__img--animate {
  animation: logo 1000ms var(--animation-timing-function);
}

@keyframes logo {
  0% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.logo__text {
  text-align: center;
  color: var(--text-logo);
}

.join-us {
  text-align: center;
}

@media screen and (max-width: 767px) {
  .join-us {
    margin-top: 30px;
  }
}

.join-us__text {
  color: var(--text-main);
  margin-bottom: 15px;
  font-size: 20px;
}

.socials {
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.socials__item {
  width: 44px;
  height: 44px;
}

.socials__item:not(:first-child) {
  margin-left: 20px;
}

.socials__link {
  width: 100%;
  height: 100%;
  color: var(--accent-color);
  background-color: var(--main-blue);
  transition: color var(--animation-duration) var(--animation-timing-function), background-color var(--animation-duration) var(--animation-timing-function);
  border: none;
  border-radius: 20px 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.socials__link:hover, .socials__link:focus {
  color: var(--main-black);
  background-color: var(--accent-color);
}

.socials__icon {
  fill: currentColor;
}

.top-btn {
  z-index: 4;
  color: #000;
  background-color: var(--accent-color-transparent);
  transition: background-color var(--animation-duration) var(--animation-timing-function), color var(--animation-duration) var(--animation-timing-function);
  border-radius: 100px 0 0;
  outline: none;
  flex-direction: column;
  align-items: center;
  padding: 12px 12px 12px 25px;
  font-size: 14px;
  display: none;
  position: fixed;
  bottom: 0;
  right: 0;
}

.top-btn:hover {
  color: var(--accent-color);
  background-color: var(--main-blue);
}

.top-btn__icon {
  width: 30px;
  fill: currentColor;
}

::-webkit-scrollbar {
  width: 1vw;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px var(--scroll-track-shadow);
  border-radius: 10px 0 0 10px;
}

::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px var(--scroll-thumb-shadow);
  border-radius: 10px 0 0 10px;
}

.color-theme {
  width: 46px;
  height: 46px;
  background: none;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 12px;
  display: flex;
  position: relative;
  box-shadow: 1px 1px 10px #885858;
}

@media screen and (max-width: 1139px) {
  .color-theme {
    margin-top: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1139px) {
  .color-theme {
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1140px) {
  .color-theme {
    margin-left: 10px;
  }
}

.sun {
  width: 40%;
  height: 40%;
  pointer-events: none;
  opacity: 0;
  background: radial-gradient(circle, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 50%, #d7fc70 50%);
  border-radius: 2px;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  position: absolute;
  transform: scale(.6)rotate(0deg);
}

.sun:before {
  content: "";
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, #fce570 30%, rgba(0, 0, 0, 0) 31%, rgba(0, 0, 0, 0) 50%, #fce570 50%);
  border-radius: 2px;
  display: block;
  position: absolute;
  transform: rotate(45deg);
}

.sun.visible {
  pointer-events: auto;
  opacity: 1;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  transform: scale(1)rotate(180deg);
}

.moon {
  width: 50%;
  height: 50%;
  pointer-events: none;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  position: absolute;
  top: 18.75%;
  left: 12.5%;
  transform: scale(.3)rotate(65deg);
  box-shadow: 9px 3px #f0f0f0;
}

.moon.visible {
  pointer-events: auto;
  opacity: 1;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  transform: scale(1)rotate(0deg);
}

.star {
  width: 0;
  height: 0;
  opacity: 0;
  border-bottom: 5px solid #f0f0f0;
  border-left: 7px solid rgba(0, 0, 0, 0);
  border-right: 7px solid rgba(0, 0, 0, 0);
  transition: all .2s ease-in .4s;
  display: block;
  position: absolute;
  top: 25%;
  left: 5%;
  transform: scale(.55)rotate(35deg);
}

.star:before {
  content: "";
  height: 0;
  width: 0;
  border-bottom: 5px solid #f0f0f0;
  border-left: 3px solid rgba(0, 0, 0, 0);
  border-right: 3px solid rgba(0, 0, 0, 0);
  display: block;
  position: absolute;
  top: -3px;
  left: -5px;
  transform: rotate(-35deg);
}

.star:after {
  content: "";
  width: 0;
  height: 0;
  color: red;
  border-bottom: 5px solid #f0f0f0;
  border-left: 7px solid rgba(0, 0, 0, 0);
  border-right: 7px solid rgba(0, 0, 0, 0);
  display: block;
  position: absolute;
  top: 0;
  left: -7px;
  transform: rotate(-70deg);
}

.moon.visible .star {
  opacity: .8;
}

.star.small {
  opacity: 0;
  transition: all .2s ease-in .45s;
  position: relative;
  top: 50%;
  left: 37.5%;
  transform: scale(.35)rotate(35deg);
}

.moon.visible .star.small {
  opacity: .7;
  transform: scale(.45)rotate(35deg);
}

.header {
  z-index: 10;
  width: 100%;
  height: 60px;
  background-color: var(--bgc-main);
  border-bottom: 1px solid var(--bgc-third);
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .header {
    height: 80px;
  }
}

.header__container {
  align-items: center;
  display: flex;
}

.menu {
  width: 280px;
}

@media screen and (max-width: 1139px) {
  .menu {
    z-index: 5;
    height: 100%;
    background-color: var(--bgc-main);
    pointer-events: none;
    transition: transform var(--animation-duration) var(--animation-timing-function);
    border-bottom-left-radius: 50px;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(120%);
    box-shadow: -3px 3px 10px #d3bfbf;
  }
}

@media screen and (min-width: 768px) {
  .menu {
    width: 500px;
  }
}

@media screen and (min-width: 1140px) {
  .menu {
    width: 100%;
  }
}

.menu.is-open {
  pointer-events: auto;
  transform: translateX(0);
}

@media screen and (max-height: 650px) {
  .menu.is-open {
    border-bottom-left-radius: 0;
    overflow: scroll;
  }
}

@media screen and (max-width: 1139px) {
  .menu__container {
    padding: 60px 20px 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1139px) {
  .menu__container {
    text-align: center;
  }
}

@media screen and (min-width: 1140px) {
  .menu__container {
    justify-content: end;
    align-items: center;
    display: flex;
  }
}

.menu__mob-btn-open {
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: flex;
}

@media screen and (min-width: 1140px) {
  .menu__mob-btn-open {
    display: none;
  }
}

.menu__mob-icon-open {
  fill: var(--text-main);
  transition: fill var(--animation-duration) var(--animation-timing-function);
}

.menu__mob-icon-open:hover, .menu__mob-icon-open:focus {
  fill: var(--accent-color);
}

.menu__mob-btn-close {
  opacity: 0;
  transition: transform 500ms var(--animation-timing-function) 500ms, opacity 500ms var(--animation-timing-function) 500ms;
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 10px;
  right: 10px;
  transform: translateX(-200%)rotateZ(0deg);
}

@media screen and (min-width: 1140px) {
  .menu__mob-btn-close {
    display: none;
  }
}

.menu.is-open .menu__mob-btn-close {
  opacity: 1;
  transform: translateY(0)rotateZ(360deg);
}

.menu__mob-icon-close {
  fill: var(--text-main);
  transition: fill var(--animation-duration) var(--animation-timing-function);
}

.menu__mob-icon-close:hover, .menu__mob-icon-close:focus {
  fill: var(--accent-color);
}

@media screen and (min-width: 1140px) {
  .nav {
    margin: 0 auto;
  }
}

@media screen and (min-width: 1140px) {
  .nav__list {
    display: flex;
  }
}

@media screen and (max-width: 1139px) {
  .nav__item {
    opacity: 0;
    transform: translateY(-50%);
  }

  .nav__item:nth-child(1) {
    transition: transform 300ms var(--animation-timing-function) var(--animation-duration), opacity 300ms var(--animation-timing-function) var(--animation-duration);
  }

  .nav__item:nth-child(2) {
    transition: transform 400ms var(--animation-timing-function) var(--animation-duration), opacity 400ms var(--animation-timing-function) var(--animation-duration);
  }

  .nav__item:nth-child(3) {
    transition: transform 500ms var(--animation-timing-function) var(--animation-duration), opacity 500ms var(--animation-timing-function) var(--animation-duration);
  }

  .nav__item:nth-child(4) {
    transition: transform 600ms var(--animation-timing-function) var(--animation-duration), opacity 600ms var(--animation-timing-function) var(--animation-duration);
  }

  .nav__item:nth-child(5) {
    transition: transform 700ms var(--animation-timing-function) var(--animation-duration), opacity 700ms var(--animation-timing-function) var(--animation-duration);
  }

  .nav__item:nth-child(6) {
    transition: transform 800ms var(--animation-timing-function) var(--animation-duration), opacity 800ms var(--animation-timing-function) var(--animation-duration);
  }
}

@media screen and (min-width: 1140px) {
  .nav__item:not(:last-child) {
    margin-right: 10px;
  }
}

.menu.is-open .nav__item {
  opacity: 1;
  transform: translateY(0);
}

.nav__link {
  text-transform: capitalize;
  color: var(--text-main);
  box-shadow: 1px 1px 1px var(--text-main);
  transition: background-color var(--animation-duration) var(--animation-timing-function), color var(--animation-duration) var(--animation-timing-function);
  border-radius: 10px 10px 30px 20px;
  padding: 10px;
  font-size: 28px;
  line-height: 40px;
  display: block;
}

.nav__link:hover, .nav__link:focus {
  background-color: var(--accent-color);
  color: var(--bgc-main);
}

@media screen and (min-width: 1140px) {
  .nav__link {
    border-radius: 30px 10px;
    padding: 10px 5px;
    font-size: 20px;
  }
}

.lang-menu {
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1139px) {
  .lang-menu {
    justify-content: center;
  }
}

@media screen and (max-width: 1139px) {
  .lang-menu {
    margin-top: 20px;
  }
}

.lang-menu__item {
  display: flex;
}

.lang-menu__item:not(:last-child) {
  margin-right: 5px;
}

.lang-menu__item:not(:last-child):after {
  content: "";
  width: 1px;
  height: 50%;
  background-color: var(--text-main);
  margin-left: 5px;
  display: block;
  transform: translateY(50%);
}

.lang-menu__btn {
